import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'
import "./App.css"

import {Hero} from "./components/Hero/Hero"
import {Navbar} from "./components/Navbar/Navbar"
function App() {
  return (
    <div className="App">
      <Router>
        <Navbar />
        <Routes>
          <Route path = "/" />
        </Routes>
        <Hero />
      </Router>
    </div>
  );
}

export default App;
